import { Injectable } from '@angular/core';
import { User } from './../models/user.class';
import { Observable, of, pipe } from 'rxjs';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import { Task } from 'src/app/user-manager/models/task.class';
import { Quest } from 'src/app/user-manager/models/quest.class';
// import { catchError, map, tap } from 'rxjs/operators';
@Injectable({
  providedIn: 'root'
})

export class UserServiceService {
  public api : string = 'http://5b0620abff98d70014f38790.mockapi.io/userlist/';
  public userList : User[];
  constructor(
    private http: HttpClient
  ) { }

  GetAllUser() : Observable<User[]>{
    return this.http.get<User[]>(this.api);
   
  }

  GetAllTask(id:number) :Observable<Task[]> {
    return this.http.get<Task[]>(this.api+id+'/tasklists');
  
  }

  GetAllQuestById(id : number, taskID : number) :Observable<Quest[]> {
    return this.http.get<Quest[]>(this.api+id + '/tasklists/' +taskID + '/QuestList');
 
  }

  getUserById(id : number) : Observable<User[]>{ 
    // const url = `${this.api}/${id}`;
    return this.http.get<User[]>(this.api + id);
  }

  addUser(user : User) : Observable<User> {
    return this.http.post<User>(this.api,user);
  }

  editUser(user : User) : Observable<User>{
    return this.http.put<User>(`${this.api}${user.id}`,user);
  }

  deleteUser(id : number) : Observable<User>{
    return this.http.delete<User>(`${this.api}${id}`);
  }

  handelError(err){
    if(err.error instanceof Error){
      console.log(`Client-side Error : ${err.error.message} ` );
    }
    else{
      console.log(`Server-side Error : ${err.error.status} - ${err.error} ` );
    }
  }
}
